//
// Rotating border
//

@keyframes spinner-border {
  to { transform: rotate(360deg); }
}

.spinner-border {
  display: inline-block;
  width: $spinner-width;
  height: $spinner-height;
  vertical-align: text-bottom;
  border: $spinner-border-width solid currentColor;
  border-right-color: transparent;
  // stylelint-disable-next-line property-blacklist
  border-radius: 50%;
  animation: spinner-border .75s linear infinite;

  // Boosted mod: override .text-light black background
  &.text-light {
    background-color: transparent;
  }
  // end mod
}

.spinner-border-sm {
  width: $spinner-width-sm;
  height: $spinner-height-sm;
  border-width: $spinner-border-width-sm;
}

//
// Growing circle
//

@keyframes spinner-grow {
  0% {
    transform: scale(0);
  }
  50% {
    opacity: 1;
    transform: none;
  }
}

.spinner-grow {
  display: inline-block;
  width: $spinner-width;
  height: $spinner-height;
  vertical-align: text-bottom;
  // stylelint-disable-next-line declaration-no-important
  background-color: currentColor !important; // Boosted mod: ensure .text-* backgrounds don't override this
  // stylelint-disable-next-line property-blacklist
  border-radius: 50%;
  opacity: 0;
  animation: spinner-grow .75s linear infinite;
}

.spinner-grow-sm {
  width: $spinner-width-sm;
  height: $spinner-height-sm;
}

// Boosted mod: Color variants
[class*="spinner-"] {
  @each $color, $value in $theme-colors {
    @if ($value != $primary) and ($value != $secondary) and ($value != $dark) and ($value != $light) {
      &.text-#{$color} {
        color: $value;
      }
    }
  }
}
// end mod
